import React from "react";
import firebase from "../firebase";
import styles from "../scss/member.module.scss";
import PropTypes from "prop-types";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faSignInAlt,
  faEnvelope,
  faLock,
  faUserPlus,
  faUser,
} from "@fortawesome/free-solid-svg-icons";
import googleIcon from "../image/google-icon-10.png";
import { setNavbarColor } from "../Utils";

const firestore = firebase.firestore();
const auth = firebase.auth();
class MemberIndex extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      loginState: true,
      email: "user@gmail.com",
      password: "123456",
      displayName: "",
      errorLoginState: "",
      errorSignupState: "",
    };
  }

  componentDidMount() {
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.props.history.push(`/profile`);
      } else {
        return;
      }
    });

    setNavbarColor("member");
  }
  googleLogin = () => {
    const provider = new firebase.auth.GoogleAuthProvider();
    firebase
      .auth()
      .signInWithPopup(provider)
      .then(() => {
        const createAt = new Date();
        firestore
          .collection("schedule")
          .doc(auth.currentUser.uid)
          .set({
            displayName: this.state.displayName,
            createAt: createAt,
            email: auth.currentUser.email,
            userID: auth.currentUser.uid,
          })
          .then(this.props.history.push(`/profile`));
      })
      .catch((error) => {
        let errorMessage = error.message;
        this.setState({
          errorLoginState: errorMessage,
        });
      });
  };

  nativeSignUp = () => {
    firebase
      .auth()
      .createUserWithEmailAndPassword(this.state.email, this.state.password)
      .then(() => {
        auth.currentUser.updateProfile({
          displayName: this.state.displayName,
        });
        const createAt = new Date();
        firestore
          .collection("schedule")
          .doc(auth.currentUser.uid)
          .set({
            displayName: this.state.displayName,
            createAt: createAt,
            email: auth.currentUser.email,
            userID: auth.currentUser.uid,
            photoURL: "",
          })
          .then(this.props.history.push(`/profile`));
      })
      .catch((error) => {
        let errorMessage = error.message;
        this.setState({
          errorSignupState: errorMessage,
        });
      });
  };
  nativeSignIn = () => {
    firebase
      .auth()
      .signInWithEmailAndPassword(this.state.email, this.state.password)
      .then(() => {
        this.props.history.push(`/profile`);
      })
      .catch((error) => {
        let errorMessage = error.message;
        this.setState({
          errorLoginState: errorMessage,
        });
      });
  };

  handleLoginChange = () => {
    if (this.state.loginState) {
      this.setState({
        loginState: false,
        email: "",
        password: "",
      });
    } else {
      this.setState({
        loginState: true,
        email: "user@gmail.com",
        password: "123456",
      });
    }
  };

  handleNameChange = (e) => {
    this.setState({
      displayName: e.target.value,
    });
  };
  handleEmailChange = (e) => {
    this.setState({
      email: e.target.value,
    });
  };
  handlePasswordChange = (e) => {
    this.setState({
      password: e.target.value,
    });
  };

  render() {
    return (
      <div className={styles.member}>
        <div className={styles.wrap}>
          <div className={styles.loginContainer}>
            <div className={styles.image}>
              <div className={styles.layer}></div>
            </div>
            <div className={styles.panel}>
              <div
                className={
                  this.state.loginState ? styles.loginPanel : styles.changeState
                }
              >
                <div className={styles.title}>
                  <h3>
                    <FontAwesomeIcon icon={faSignInAlt} />
                    
                  </h3>
                </div>
                <div className={styles.container}>
                  <div className={styles.signinTitle}>
                    <p></p>
                  </div>
                  <div className={styles.signinItem}>
                    <FontAwesomeIcon icon={faEnvelope} />
                    <input
                      type="email"
                      id="sign-in-email"
                      placeholder=""
                      onChange={(e) => this.handleEmailChange(e)}
                      value={this.state.email}
                    />
                  </div>

                  <div className={styles.signinItem}>
                    <FontAwesomeIcon icon={faLock} />
                    <input
                      type="password"
                      id="sign-in-email"
                      placeholder=""
                      onChange={(e) => this.handlePasswordChange(e)}
                      value={this.state.password}
                    />
                  </div>
                  <div className={styles.alert}>
                    {this.state.errorLoginState}
                  </div>
                  <button
                    id="signInBtn"
                    className={styles.btn}
                    onClick={this.nativeSignIn}
                  >
                    
                  </button>
                </div>
                <div className={styles.changeSingup}>
                  <p></p>
                  <button onClick={this.handleLoginChange}>
                    
                  </button>
                </div>
                <div className={styles.divider}>
                  <span></span>
                  <p></p>
                  <span></span>
                </div>
                <div className={styles.socialSignin} onClick={this.googleLogin}>
                  <img src={googleIcon} alt="google-icon" />
                  <p>Google </p>
                </div>
              </div>

              <div
                className={
                  this.state.loginState
                    ? styles.changeState
                    : styles.signupPanel
                }
              >
                <div className={styles.title}>
                  <h3>
                    <FontAwesomeIcon icon={faUserPlus} />
                    
                  </h3>
                </div>
                <div className={styles.container}>
                  <div className={styles.signinItem}>
                    <FontAwesomeIcon icon={faUser} />
                    <input
                      type="text"
                      id="sign-up-name"
                      placeholder=""
                      onChange={(e) => this.handleNameChange(e)}
                      value={this.state.displayName}
                    />
                  </div>
                  <div className={styles.signinItem}>
                    <FontAwesomeIcon icon={faEnvelope} />
                    <input
                      type="email"
                      id="sign-up-email"
                      placeholder=""
                      onChange={(e) => this.handleEmailChange(e)}
                      value={this.state.email}
                    />
                  </div>

                  <div className={styles.signinItem}>
                    <FontAwesomeIcon icon={faLock} />
                    <input
                      type="password"
                      id="sign-up-password"
                      placeholder=""
                      onChange={(e) => this.handlePasswordChange(e)}
                      value={this.state.password}
                    />
                  </div>
                  <div className={styles.alert}>
                    {this.state.errorSignupState}
                  </div>
                  <button
                    id="signUpBtn"
                    className={styles.btn}
                    onClick={this.nativeSignUp}
                  >
                    
                  </button>
                </div>
                <div className={styles.changeSingup}>
                  <p></p>
                  <button onClick={this.handleLoginChange}></button>
                </div>
                <div className={styles.divider}>
                  <span></span>
                  <p></p>
                  <span></span>
                </div>
                <div className={styles.socialSignin} onClick={this.googleLogin}>
                  <img src={googleIcon} alt="google-icon" />
                  <p>Google </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

MemberIndex.propTypes = {
  history: PropTypes.object.isRequired,
};

export default MemberIndex;
